.header{
    width: 100%;
    display: block;
    background-color: rgba(0, 0, 0, 0.705);
    height: 40px;
    position: fixed;
    top: 0;
left: 0;    
display: flex;
justify-content: center;
align-items: center;
}

.header ul{
display: flex;
list-style: none;
justify-content: space-around;
width: 100%;

}

.header a{
    text-decoration: none;
    color: white;
    font-size: 24px;
}

.header a:hover{
    color: #ffb302;
    transition: all 0.1s ease-in;
}

