* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: url(../imgs/rome.jpg);
  background-size: cover;
  background-blend-mode: multiply;
  background-color: rgba(0, 0, 0, 0.753);
  background-repeat: no-repeat;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-container {
  flex-direction: column;
  margin: 3rem auto;
}
.content {
  color: white;
  margin: 1rem;
  text-align: center;
  padding: 1rem;
}

.greeter {
  display: table;
  margin: 0 auto 2rem auto;
  max-width: 40vw;
  border: 2px solid #ffb302;
  font-weight: lighter;
  line-height: 30px;
  background-color: rgba(5, 5, 5, 0.288);
  box-shadow: 2px 2px 10px black;
}

.greeter p {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.header {
  color: white;
  font-size: 2.5rem;
  text-shadow: 2px 2px 10px black;
}

.line {
  width: 5vw;
  height: 0.5vh;
  background-color: #ffb302;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: 2px 2px 10px black;
}

.nav-main {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 60vw;
  margin: 3rem auto;
  flex-wrap: wrap;
}

.nav-main a {
  display: flex;
  justify-content: center;
  word-wrap: break-word;
  padding: 2rem 4rem;
  margin: 1rem;
  font-size: 1.4rem;
  background: #0000009f;
  color: #fff;
  border: 3px solid orange;
  text-decoration: none;
  text-align: center;
  border-radius: 10px;
  width: 30%;
  box-shadow: 2px 2px 10px black;
}
