.container{
    margin-top: 80px;
    margin-left: 50px;
    color: white;
    display: flex;
    
}

.list{
    list-style: none;
display: flex;
flex-wrap: wrap;
width: 500px;
justify-content: center;

}

.list li{
padding: 10px;

}

.list a{
text-decoration: none;
color: white;
border: 1px solid white;
border-radius: 5px;
padding: 5px;
}

.list a:hover{
    color: black;
    transition: all 0.1s ease-in;
}

